.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
    position: relative;
    overflow: hidden;
    transition: transform 0.3s 
  }

  .product__rating {
    display: flex;
  }
  
  .product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
    transition: transform 0.3s ease-in-out;
  }
  
  .product:hover img {
    transform: scale(1.1);
  }

  .product > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    transition: ease;
  }
  
  .product > button:hover {
    cursor: pointer;
    transition: 200ms;
    background-color: #a88734;

  }
  .product__price {
    margin-top: 5px;
  }
  
  .product__info {
    height: 100px;
    margin-bottom: 15px;
  }